/* Styles from the GreenSock website */
body {
	font-weight: 300;
}
.ipsType_right {
	text-align: right;
}
.ipsType_center {
	text-align: center;
}
.cInnerContent {
	max-width: 1240px;
	margin-left: auto;
	margin-right: auto;
}
.ipsSpacer_bottom_double {
	margin-bottom: 30px;
}
.ipsGrid {
	display: inline-block;
	display: -ms-flexbox;
	display: flex;
	flex-wrap: wrap;
}
.ipsGrid::before,
.ipsGrid::after {
	display: table;
	content: '';
	line-height: 0;
}
.ipsGrid > [class*='ipsGrid_span'] {
	display: block;
	width: 100%;
	min-height: 30px;
	box-sizing: border-box;
}
.ipsGrid > .ipsGrid_span5 {
	width: 40.42553191489362%;
}
.ipsGrid > .ipsGrid_span7 {
	width: 57.44680851063829%;
}
.ipsGrid > [class*='ipsGrid_span'] {
	float: left;
	margin-left: 2%;
}
.ipsGrid > [class*='ipsGrid_span']:first-child {
	margin-left: 0;
}
.feature {
	display: flex;
	align-items: center;
}
.card {
	position: relative;
	// margin: 10px;
	border: 1px solid #cccccc;
	border-radius: 8px;
	overflow: hidden;
	background: #ffffff;
	box-shadow: 1px 1px 5px 1px #cccccc;
	display: flow-root;
	/*   transition: 0.3s; */
	img {
		position: absolute;
		width: 100%;
		height: 100%;
		max-width: 100%;
		object-fit: cover;
	}
}
.featured-image-container .card {
	height: 0;
	padding-bottom: calc(100% - 10px);
}
h2.heading_large {
	font-size: 1.8em;
}
img {
	max-width: 100%;
}

.header-section {
	margin: 200px auto;
}

.gs_reveal {
	opacity: 0;
	visibility: hidden;
	will-change: transform, opacity;
}
